import React, { useState,useEffect, useRef } from 'react';
import { RedditOutlined , AppstoreTwoTone, OrderedListOutlined,UserOutlined,PlusCircleTwoTone } from '@ant-design/icons';
import { Menu,Modal } from 'antd';
import Department from './Department';

const Header = () => {
    //   : ,  ,  ,  ,  
    const [current, setCurrent] = useState("");
    const handleClick = e => {
        console.log('click ', e);
        setCurrent(e.key);
    };

    // 
    const [isModalVisible, setIsModalVisible] = useState(false);
    const changeModal = () => {
        setIsModalVisible(false);
    }
    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleOk = () => {
        setIsModalVisible(false);
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    }; 


    return (
        <Menu  onClick={handleClick} selectedKeys={[current]} mode="horizontal">
            <Menu.Item key="" icon={<AppstoreTwoTone />}>
                
            </Menu.Item>
            <Menu.Item key=" " icon={<OrderedListOutlined />} >
                 
            </Menu.Item>
            <Menu.Item  key=" " icon={<RedditOutlined />}>
                <a href="https://ant.design" target="_blank" rel="noopener noreferrer">
                     
                </a>
            </Menu.Item>
            <Menu.Item key=" " icon={<PlusCircleTwoTone twoToneColor="#eb2f96"/>} >
                 
            </Menu.Item>
            <Menu.Item onClick={showModal} style={{ marginLeft: 'auto' }} key="" icon={<UserOutlined />}>
                
            </Menu.Item>
            <Modal visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} footer={null} >
				<Department modal changeModal={changeModal} />
            </Modal>
            
        </Menu>
    );
}
    
export default Header;